/* type selectors */
html {
	background-repeat: no-repeat;
    background-image: -moz-linear-gradient(to bottom, white 0%, yellow 50%, rgb(30, 255, 0) 100%);
	background-image: -webkit-linear-gradient(to bottom, white 0%, yellow 50% rgb(30, 255, 0) 100%);	
	background-image: -o-linear-gradient(to bottom, white 0%, yellow 50% rgb(30, 255, 0) 100%);
	background-image: linear-gradient(to bottom, white 0%, yellow 50%, rgb(30, 255, 0) 100%);
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
body {
	font-family: Verdana, Arial, Helvetica, sans-serif;
    font-size: 100%;
	width: auto;
	background-color: transparent;
	margin: 15px 8px;
	padding: 15px 0;
	border: none
}
h1, h2, p {
	margin: 0;
	padding: 0 0 0 0.1em;
}
section, aside {
	margin-top: 1.5em;
	margin-bottom: 1em;	
}

a {
	font-weight: bold;
}
button { 
    color: black;
}

button:hover, button:focus { color: blue; }

p {
	font-size: 95%;
	padding: .25em 0;
}
em { font-weight: bold; }

/* the styles for the header */
header { 
	padding-bottom: 2em;
}
h1 {
	font-size: 220%;
    color: #f2972e;
    text-shadow: 2px 3px 0 black;
    margin-left: 120px;
	margin-bottom: .25em;
}
h2 {
	font-size: 1.5em;
	margin-top: 0.83em;
	margin-bottom: 0.83em;
	margin-left: 0;
	margin-right: 0;
	font-weight: bold;
}